import { Fragment, h, JSX } from '@stencil/core';
import { handleFilledIcon, isShopAvailable } from '../../utility/helpers/shop-helper';
import animationManager from '../../services/animations/animation-manager';
import { getRightTheme } from '../../utility/helper';
import { navContentStore } from '../../state/nav-content-store';
import { pureButtonSkeleton } from '../../utility/skeleton';
import { ContextualIconProps, ContextualIconResponse } from '../../utility/constants/app';
import { EventActions, NavigationModeType, NavigationThemeType } from '../../utility/constants';
import { shopStateStore } from '../../state/shop-state-store';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';

export default function shopIcons({
  locale,
  app,
  mode,
  theme,
  isNavigationLoaded,
  isDesktop
}: ContextualIconProps): ContextualIconResponse {
  const { shopInformation } = shopStateStore.state;
  const shopCartLink = navContentStore.state.shop?.cart || '';
  const shopWishlistLink = navContentStore.state.shop?.wishlist?.link || '';

  function getAriaLabelForShoppingBag(): string {
    return `My shopping bag, ${shopInformation.numberOfShoppingItems} items, estimated subtotal ${shopInformation.subTotal}`;
  }
  function getAriaLabelForWishlist(): string {
    return `My wishlist, ${shopInformation.wishlistItemsNumber} items`;
  }

  return {
    leftIcons: isShopAvailable(locale, app) ? (
      <div class="icon">
        <phn-p-button-pure
          icon="search"
          hide-label="true"
          stretch="true"
          aria="{ 'aria-haspopup': 'dialog', 'aria-label' : 'open the shop search field' }"
          data-test-id="search-button"
          onClick={() => {
            animationManager.play('openContextualDrawer');
            sendDataLayerEventContextualDrawer();
          }}
          style={isNavigationLoaded ? {} : { display: 'none' }}
          theme={getRightTheme(theme, mode)}
        />
        {!isNavigationLoaded ? pureButtonSkeleton() : null}
      </div>
    ) : null,
    rightIcons: isShopAvailable(locale, app) ? (
      <Fragment>
        {isDesktop ? (
          <div class="icon">
            <phn-p-link-pure
              target={'_self'}
              hide-label="true"
              icon="none"
              theme={getRightTheme(theme, mode)}
              style={isNavigationLoaded ? {} : { display: 'none' }}
              className="wishlist-link"
              data-test-id="wishlist-link"
              onClick={() => {
                sendDataLayerEventShoppingCart(shopWishlistLink, 'mainmenu.shop.wishlist', 'Wishlist');
              }}
            >
              <a href={shopWishlistLink} aria-label={getAriaLabelForWishlist()}>
                <div class="icon-flex">
                  <BadgeCount numberOfItems={shopInformation.wishlistItemsNumber} theme={theme} mode={mode} />
                  <phn-p-icon
                    theme={getRightTheme(theme, mode)}
                    name={handleFilledIcon('heart', shopInformation.wishlistItemsNumber)}
                    data-test-id="wishlist-link-icon"
                  />
                </div>
              </a>
            </phn-p-link-pure>
          </div>
        ) : null}
        {!isNavigationLoaded && isDesktop ? pureButtonSkeleton() : null}

        <div class="icon">
          <phn-p-link-pure
            target={'_self'}
            hide-label="true"
            icon="none"
            theme={getRightTheme(theme, mode)}
            style={isNavigationLoaded ? {} : { display: 'none' }}
            class="shopping-cart-link"
            data-test-id="shopping-cart-link"
            onClick={() => {
              sendDataLayerEventShoppingCart(shopCartLink, 'mainmenu.shop.shoppingbag', 'Shopping cart');
            }}
          >
            <a href={shopCartLink} aria-label={getAriaLabelForShoppingBag()}>
              <div class="icon-flex">
                <BadgeCount numberOfItems={shopInformation.numberOfShoppingItems} theme={theme} mode={mode} />
                <phn-p-icon
                  theme={getRightTheme(theme, mode)}
                  name={handleFilledIcon('shopping-bag', shopInformation.numberOfShoppingItems)}
                  data-test-id="shopping-cart-link-icon"
                />
              </div>
            </a>
          </phn-p-link-pure>
          {!isNavigationLoaded ? pureButtonSkeleton() : null}
        </div>
      </Fragment>
    ) : null
  };
}

const BadgeCount = ({
  numberOfItems = 0,
  theme,
  mode
}: {
  numberOfItems: number;
  theme: NavigationThemeType;
  mode: NavigationModeType;
}): JSX.Element => {
  const shoppingItemCountOver99 = '99+';
  const displayedCount = numberOfItems < 99 ? numberOfItems : shoppingItemCountOver99;

  if (numberOfItems <= 0) return '';

  return (
    <span class={`shopping-item-count ${numberOfItems < 10 ? '' : 'pill'} ${getRightTheme(theme, mode)}`}>
      {displayedCount}
    </span>
  );
};

function sendDataLayerEventContextualDrawer() {
  globalDataLayerService.pushDataLayerEvent(EventActions.CONTEXTUAL_BUTTON_CLICK, {
    componentClick: {
      clickElementId: 'mainmenu.shop.search',
      clickElementName: 'Shop search'
    }
  });
}

function sendDataLayerEventShoppingCart(url: string, elementId: string, elementName: string) {
  globalDataLayerService.pushDataLayerEvent(EventActions.CONTEXTUAL_BUTTON_CLICK, {
    componentClick: {
      clickElementId: elementId,
      clickElementName: elementName,
      targetUrl: url
    }
  });
}
